import React, { Component } from 'react';
import './index.css';
import Menu from '../../components/Menu';

class HomeSection extends Component {

  scrollTo(e) {
    e.preventDefault();
    document.querySelector('#aboutSection').scrollIntoView({
      behavior: 'smooth' 
    });
  }


  render() {
    return (
      <section className="section homeSection" id="homeSection">
        <div className="background"></div>
        <Menu />
        {/* <img src="assets/me.jpeg" alt="" className="homeSection__photo" /> */}
        <h1 className="homeSection__name">  &lt;talita-oliveira/&gt; </h1>
        <p className="homeSection__description">Web Developer</p>
        <div className="bouncy-arrow">
          <a href="#aboutSection" className="scroll" onClick={(e) => this.scrollTo(e)}>
            <i className="fas fa-chevron-circle-down"></i>
          </a>
        </div>
      </section>
    );
  }
}

export default HomeSection;